import { graphql } from 'gatsby';
import TagGS from '../containers/TagGS';

export default TagGS;

export const pageQuery = graphql`
  query PostsByTagQuery(
    $skip: Int!
    $limit: Int!
    $slug: String!
    $regex: String!
  ) {
    collectionInfo: googleTagsSheet(slug: { eq: $slug }) {
      name
      slug
    }
    posts: allGooglePostsSheet(
      filter: {tags: {regex: $regex}}
      limit: $limit
      skip: $skip
    ) {
      nodes {
        title
        slug
        icon
        filters
      }
      pageInfo {
        pageCount
        hasPreviousPage
        hasNextPage
        currentPage
      }
      totalCount
    }
  }
`;
